import React, { useState } from 'react'
import { StaticRoutes } from 'Shared/constants/Routes'
import { BigWithIconButton } from 'Shared/components/Button'
import InviteModal from 'Employer/components/InviteModal/InviteModal'
import { EmployerRoles, EmployerPermissions } from 'Shared/constants/User'
import LimitedAccessWrapperConnector from 'Employer/connectors/Components/LimitedAccessWrapperConnector'
import { t } from 'i18next'
import InviteToApplyConnector from 'Employer/connectors/Forms/InviteToApply/InviteToApplyFormConnector'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import './CandidateProfileInviteToApply.scss'
import mixpanel from 'mixpanel-browser'

function CandidateProfileInviteToApply (props) {
  const [ inviteModalVisibility, setInviteModalVisibility ] = useState(false)

  const showInviteModal = () => {setInviteModalVisibility(true); mixpanel.track("Button Click - Invite To Apply");}
  const hideInviteModal = () => setInviteModalVisibility(false)

  const redirectToBuyCreditsPage = e => {
    e.stopPropagation()
    e.preventDefault()
    props.history.push(StaticRoutes.creditsReview)
  }

  const preventRedirect = e => {
    e.stopPropagation()
    e.preventDefault()
    mixpanel.track("Button Click - Invite To Apply")
  }

  const getButton = () => {
    const { canUseDatabaseSearch, organisationRole, adminFullName, organisationName } = props

    switch (true) {
      case canUseDatabaseSearch:
        return <LimitedAccessWrapperConnector
          onClick={preventRedirect}
          placeholderComponent={<BigWithIconButton className='candidate-profile-invite-to-apply__btn' primary>
            <i className='ion-md-person-add' />{t('common.inviteToApply')}
          </BigWithIconButton>}
          allowedFor={[EmployerPermissions.cvSearch]}
        >
          <BigWithIconButton className='candidate-profile-invite-to-apply__btn' primary onClick={showInviteModal}>
            <i className='ion-md-person-add' />{t('common.inviteToApply')}
          </BigWithIconButton>
        </LimitedAccessWrapperConnector>
      case !canUseDatabaseSearch && organisationRole === EmployerRoles.organisation_manager:
        return <Tooltip notAllowed content={t('tooltips.teamMemberAccess.cvSearchBuy', { adminFullName, organisationName })} trigger={'mouseenter'}>
          <BigWithIconButton className='candidate-profile-invite-to-apply__btn' disabled>
            <i className='ion-md-person-add' />{t('common.inviteToApply')}
          </BigWithIconButton>
        </Tooltip>
      default:
        return <Tooltip notAllowed content={t('accessPermissions.button')}>
          <BigWithIconButton className='candidate-profile-invite-to-apply__btn' onClick={redirectToBuyCreditsPage}><i className='ion-md-person-add' />{t('common.inviteToApply')}</BigWithIconButton>
        </Tooltip>
    }
  }

  const { shouldUnlock, candidate } = props

  return (
    <div className='candidate-profile-invite-to-apply'>
      {getButton()}
      <InviteModal
        title={t('modals.inviteToApply.title')}
        description={t('modals.inviteToApply.description')}
        isModalVisible={inviteModalVisibility}
        hideInviteModal={hideInviteModal}
        closeOnSubmit={false}
        shouldUnlock={shouldUnlock}
      >
        <InviteToApplyConnector candidate={candidate} />
      </InviteModal>
    </div>
  )
}

export default CandidateProfileInviteToApply



