import React from 'react'
import CandidateProfileHeaderActionButtonsMobile from './CandidateProfileHeaderActionButtons.mobile'
import CandidateProfileHeaderActionButtonsWeb from './CandidateProfileHeaderActionButtons'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const CandidateProfileHeaderActionButtons = props => props.isMobile ? <CandidateProfileHeaderActionButtonsMobile {...props} /> : <CandidateProfileHeaderActionButtonsWeb {...props} />

export default withDeviceWidthDetector(CandidateProfileHeaderActionButtons)



