import React from 'react'
import CandidateDetailsViewMobile from './CandidateDetailsView.mobile'
import CandidateDetailsViewWeb from './CandidateDetailsView'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const CandidateDetailsView = props => props.isMobile ? <CandidateDetailsViewMobile {...props} /> : <CandidateDetailsViewWeb {...props} />

export default withDeviceWidthDetector(CandidateDetailsView)



