import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileDocuments from 'Employer/components/CandidateProfileDetails/CandidateProfileDocuments'
import { getCandidateDefaultCvOrAllCvs } from 'Shared/selectors/files'
import { shouldUnlockCandidate } from 'Shared/selectors/candidates'
import { handleCandidateUnlock, UNLOCK_CANDIDATE_SUCCESS } from 'Shared/ducks/user/candidate'
import { downloadFile, handleView } from 'Shared/helpers/files'

const mapStateToProps = (state, ownProps) => ({
  documents: getCandidateDefaultCvOrAllCvs(state),
  shouldUnlock: shouldUnlockCandidate(state, ownProps.match.params.candidateId)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  handleCandidateUnlock: (userId, documentId, view) => () => dispatch(handleCandidateUnlock(ownProps.match.params.candidateId))
    .then(({ type }) => {
      if (type === UNLOCK_CANDIDATE_SUCCESS) {
        return view ? handleView(userId, documentId) : downloadFile(userId, documentId)
      }
    })
})

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(CandidateProfileDocuments)



