import React, { useEffect } from 'react'
import { t } from 'i18next'
import { isEmpty, not } from 'ramda'
import PropTypes from 'prop-types'
import { withRouter } from 'react-router-dom'
import { AuhtorisedEmployerRoutes } from 'Shared/constants/Routes'
import withTranslation from 'Shared/hoc/withTranslations'
import Container from 'Shared/components/Container'
import BackButton from 'Shared/components/BackButton/BackButton'
import withLoader from 'Shared/hoc/withLoader/withLoader'
import { CandidateProfileViewLoader } from 'Shared/components/Main/MainLoaders'
import { pathWithoutIdentifier } from 'Shared/helpers/locationIdentifier'
import CandidateProfileHeaderConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileHeaderConnector'
import { addLightBackground, removeLightBackground } from 'Shared/helpers/layout'
import CandidateProfileNavbarConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileNavbarConnector'
import { Element } from 'react-scroll'
import { ProfilePreviewNavigationIds } from 'Candidate/constants/ProfilePreviewNavigation'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import CandidateProfileAboutConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileAboutConnector'
import CandidateProfileGeneralInfoConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileGeneralInfoConnector'
import CandidateProfileCvDocumentConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileCvDocumentConnector'
import CandidateProfileEducationConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileEducationConnector'
import CandidateProfileCertificatesConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileCertificatesConnector'
import CandidateProfileExperienceConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileExperienceConnector'
import CandidateProfileVolunteeringConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileVolunteeringConnector'
import CandidateProfileSkillsConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileSkillsConnector'
import CandidateProfileInterestsConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileInterestsConnector'
import CandidateProfileLanguagesConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileLanguagesConnector'
import CandidateProfileVideoCvConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileVideoCvConnector'
import CandidateProfileAdditionalDocumentsConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileAdditionalDocumentsConnector'
import CandidateProfileWebsitesConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileWebsitesConnector'
import './CandidateDetailsView.scss'

const CandidateDetailsView = (props) => {
  useEffect(() => {
    addLightBackground()
    props.fetchCandidate()
    props.setCandidateAsViewed()
  }, [])

  useEffect(() => {
    if (not(isEmpty(props.candidateDetails.id))) {
      props.finishLoading(props.candidateDetails)
    }
  }, [props.candidateDetails])

  useEffect(() => {
    return () => {
      removeLightBackground()
    }
  }, [])

  const goBack = () => {
    const { opener } = window
    const backToCandidates = opener
      ? `${pathWithoutIdentifier(opener.location.pathname)}${opener.location.search}`
      : AuhtorisedEmployerRoutes.candidates

    return props.history.push(backToCandidates)
  }

  const {
    canUseDatabaseSearch,
    hasCandidateAnyCertificates,
    hasCandidateAnyExperience,
    hasCandidateAnyVolunteering,
    hasCandidateAnySkills,
    hasCandidateAnyLanguages,
    hasCandidateVideoCv,
    hasAnyAdditionalDocuments,
    hasCandidateAnyInterests,
    hasCandidateAnyWebsites
  } = props

  return (
    <section className='candidate-details-view__container parsing--hidden'>
      <Container>
        <BackButton button onClick={goBack} name={t('employer.candidates.backToCandidates')}
        />
        <CandidateProfileHeaderConnector />
      </Container>
      <CandidateProfileNavbarConnector />
      <Container>
        <Element name={ProfilePreviewNavigationIds.about}>
          <ProfilePreviewSection isWrapped header={t('common.about')}>
            <CandidateProfileAboutConnector />
            <CandidateProfileGeneralInfoConnector />
          </ProfilePreviewSection>
        </Element>
        {
          canUseDatabaseSearch && (
            <Element name={ProfilePreviewNavigationIds.resume}>
              <ProfilePreviewSection isWrapped header={t('candidate.profile.cvFiles.resume')}>
                <CandidateProfileCvDocumentConnector />
              </ProfilePreviewSection>
            </Element>
          )
        }
        <Element name={ProfilePreviewNavigationIds.education}>
          <ProfilePreviewSection isWrapped header={t('profile.education.title')}>
            <CandidateProfileEducationConnector />
          </ProfilePreviewSection>
          {
            hasCandidateAnyCertificates && (
              <ProfilePreviewSection isWrapped header={t('profile.education.sections.additionalCertifications.label')}>
                <CandidateProfileCertificatesConnector />
              </ProfilePreviewSection>
            )
          }
        </Element>
        {}
        <Element name={ProfilePreviewNavigationIds.experience}>
          {
            hasCandidateAnyExperience && (
              <ProfilePreviewSection isWrapped header={t('profile.experience.title')}>
                <CandidateProfileExperienceConnector />
              </ProfilePreviewSection>
            )
          }
          {
            hasCandidateAnyVolunteering && (
              <ProfilePreviewSection isWrapped header={t('profile.experience.sections.volunteering.label')}>
                <CandidateProfileVolunteeringConnector />
              </ProfilePreviewSection>
            )
          }
          {
            hasCandidateAnySkills && (
              <ProfilePreviewSection isWrapped header={t('profile.experience.sections.skills.label')}>
                <CandidateProfileSkillsConnector />
              </ProfilePreviewSection>
            )
          }
        </Element>
        {
          hasCandidateAnyLanguages && (
            <Element name={ProfilePreviewNavigationIds.languages}>
              <ProfilePreviewSection isWrapped header={t('profile.additional.sections.languages.label')}>
                <CandidateProfileLanguagesConnector />
              </ProfilePreviewSection>
            </Element>
          )
        }
        {
          canUseDatabaseSearch && (hasCandidateVideoCv || hasAnyAdditionalDocuments) && (
            <Element name={ProfilePreviewNavigationIds.videoCv}>
              {
                hasCandidateVideoCv && (
                  <ProfilePreviewSection isWrapped header={t('profile.media.sections.videoCV.label')}>
                    <CandidateProfileVideoCvConnector />
                  </ProfilePreviewSection>
                )
              }
              {
                hasAnyAdditionalDocuments && (
                  <ProfilePreviewSection isWrapped header={t('profile.additional.sections.additionalDocuments.label')}>
                    <CandidateProfileAdditionalDocumentsConnector />
                  </ProfilePreviewSection>
                )
              }
            </Element>
          )
        }
        {
          hasCandidateAnyInterests && (
            <Element name={ProfilePreviewNavigationIds.interests}>
              <ProfilePreviewSection isWrapped header={t('profile.experience.sections.interests.label')}>
                <CandidateProfileInterestsConnector />
              </ProfilePreviewSection>
            </Element>
          )
        }
        {
          canUseDatabaseSearch && hasCandidateAnyWebsites && (
            <Element name={ProfilePreviewNavigationIds.social}>
              <ProfilePreviewSection header={t('candidate.profile.socialLinks.social')}>
                <CandidateProfileWebsitesConnector />
              </ProfilePreviewSection>
            </Element>
          )
        }
      </Container>
    </section>
  )
}

CandidateDetailsView.defaultProps = {
  candidateDetails: {
    id: ''
  }
}

CandidateDetailsView.propTypes = {
  candidateDetails: PropTypes.object,
  fetchCandidate: PropTypes.func.isRequired
}

export default withRouter(withTranslation(withLoader(CandidateDetailsView, CandidateProfileViewLoader)))



