import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileInviteToApply from 'Employer/components/CandidateProfileDetails/CandidateProfileInviteToApply'
import { organisationAdminEmployerFullName } from 'Shared/selectors/employers'
import { shouldUnlockCandidate, getCandidateDetails } from 'Shared/selectors/candidates'
import { hasDatabaseSearchAvailable } from 'Shared/selectors/membership'
import { getEmployerOrganisationRole, getUserOrganisationId } from 'Shared/selectors/user'
import { getOrganisationName } from 'Shared/selectors/organisations'

const mapStateToProps = (state, ownProps) => {
  const candidateId = ownProps.match.params.candidateId
  const candidate = getCandidateDetails(state, candidateId)
  const organisationId = getUserOrganisationId(state)

  return ({
    adminFullName: organisationAdminEmployerFullName(state, organisationId),
    candidate,
    canUseDatabaseSearch: hasDatabaseSearchAvailable(state),
    shouldUnlock: shouldUnlockCandidate(state, candidateId),
    organisationRole: getEmployerOrganisationRole(state),
    organisationName: getOrganisationName(state, organisationId)
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileInviteToApply)



