import React, { Fragment } from 'react'
import CandidateProfileAvailabilityConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileAvailabilityConnector'
import CandidateProfileInviteToApplyConnector from 'Employer/connectors/CandidateProfileDetails/CandidateProfileInviteToApplyConnector'

function CandidateProfileHeaderActionButtons (props) {
  return (
    <Fragment>
      <CandidateProfileInviteToApplyConnector />
      <CandidateProfileAvailabilityConnector />
    </Fragment>
  )
}

export default CandidateProfileHeaderActionButtons



