import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileTagList from 'Employer/components/CandidateProfileDetails/CandidateProfileTagList'
import { getUserSkills } from 'Shared/selectors/skills'

const mapStateToProps = (state, ownProps) => {
  const candidateId = ownProps.match.params.candidateId

  return ({
    tagsList: getUserSkills(state),
    withHighlights: true,
    candidateId
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileTagList)



