import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileCertificates from 'Employer/components/CandidateProfileDetails/CandidateProfileCertificates'
import { getCandidateCertificates } from 'Shared/selectors/certificates'

const mapStateToProps = (state, ownProps) => {
  const candidateId = ownProps.match.params.candidateId

  return ({
    certificates: getCandidateCertificates(state, candidateId),
    withHighlights: true,
    candidateId
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileCertificates)



