import React from 'react'
import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileHeader from 'Employer/components/CandidateProfileDetails/CandidateProfileHeader'
import {
  getCandidateDetails,
  getCandidateLocation,
  getCandidateLastName,
  getCandidateFirstName,
  hasCandidateOlivPlus
} from 'Shared/selectors/candidates'
import { getCandidateMajorsList } from 'Shared/selectors/education'
import CandidateUnlockContactButtonsConnector from 'Employer/connectors/CandidateProfileDetails/CandidateUnlockContactButtonsConnector'
import CandidateProfileHeaderActionButtons from 'Employer/components/CandidateProfileDetails/CandidateProfileHeaderActionButtons'
import { hasDatabaseSearchAvailable } from 'Shared/selectors/membership'

const mapStateToProps = (state, ownProps) => {
  const candidateId = ownProps.match.params.candidateId

  return ({
    details: getCandidateDetails(state, candidateId),
    firstName: getCandidateFirstName(state, candidateId),
    lastName: getCandidateLastName(state, candidateId),
    major: getCandidateMajorsList(state, candidateId),
    location: getCandidateLocation(state, candidateId),
    id: candidateId,
    hasDatabaseSearchAvailable: hasDatabaseSearchAvailable(state),
    contactSection: () => <CandidateUnlockContactButtonsConnector />,
    asideSection: () => <CandidateProfileHeaderActionButtons />,
    hasOlivPlus: hasCandidateOlivPlus(state, candidateId)
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileHeader)



