import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { compose } from 'redux'
import CandidateUnlockContactButtons from 'Employer/components/CandidateProfileDetails/CandidateUnlockContactButtons'
import {
  getCandidateMail,
  getCandidatePhone,
  shouldUnlockCandidate
} from 'Shared/selectors/candidates'
import { hasDatabaseSearchAvailable } from 'Shared/selectors/membership'

const mapStateToProps = (state, ownProps) => {
  const candidateId = ownProps.match.params.candidateId

  return ({
    email: getCandidateMail(state, candidateId),
    phoneNumber: getCandidatePhone(state, candidateId),
    candidateId,
    canUseDatabaseSearch: hasDatabaseSearchAvailable(state),
    shouldUnlock: shouldUnlockCandidate(state, candidateId)
  })
}

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateUnlockContactButtons)



