import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import CandidateDetailsView from 'Employer/views/CandidateDetailsView'
import { fetchCandidate } from 'Shared/ducks/candidates'
import { hasDatabaseSearchAvailable } from 'Shared/selectors/membership'
import { setCandidateAsViewed } from 'Shared/ducks/candidateSearchFilters'
import withAlgoliaHighlight from 'Algolia/hoc/withAlgoliaHighlight'
import { hasCandidateAnyCertificates } from 'Shared/selectors/certificates'
import { hasCandidateAnyExperience, hasCandidateAnyVolunteering } from 'Shared/selectors/experience'
import { hasCandidateAnySkills } from 'Shared/selectors/skills'
import { hasCandidateAnyLanguages } from 'Shared/selectors/languages'
import { hasCandidateVideoCv } from 'Shared/selectors/candidates'
import { hasAnyAdditionalDocuments } from 'Shared/selectors/files'
import { hasCandidateAnyInterests } from 'Shared/selectors/interests'
import { hasCandidateAnyWebsites } from 'Shared/selectors/links'

const mapStateToProps = (state, ownProps) => {
  const candidateId = ownProps.match.params.candidateId

  return ({
    candidateId,
    canUseDatabaseSearch: hasDatabaseSearchAvailable(state),
    candidateDetails: state.candidates.entries[candidateId],
    hasCandidateAnyCertificates: hasCandidateAnyCertificates(state, candidateId),
    hasCandidateAnyExperience: hasCandidateAnyExperience(state, candidateId),
    hasCandidateAnyVolunteering: hasCandidateAnyVolunteering(state, candidateId),
    hasCandidateAnySkills: hasCandidateAnySkills(state),
    hasCandidateAnyLanguages: hasCandidateAnyLanguages(state, candidateId),
    hasCandidateVideoCv: hasCandidateVideoCv(state, candidateId),
    hasAnyAdditionalDocuments: hasAnyAdditionalDocuments(state),
    hasCandidateAnyInterests: hasCandidateAnyInterests(state),
    hasCandidateAnyWebsites: hasCandidateAnyWebsites(state, candidateId)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchCandidate: () => dispatch(fetchCandidate(ownProps.match.params.candidateId)),
  setCandidateAsViewed: () => dispatch(setCandidateAsViewed(ownProps.match.params.candidateId))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(withAlgoliaHighlight(CandidateDetailsView)))



